}

.dark-logo {
	display: none;
}

@media (max-width: 767px) {
	.header-right .responsive-navbar .navbar-collapse {
		position: fixed;
		left: 0;
		background: #fff;
		width: 100%;
		margin-top: 118px;
		border-bottom: 1px solid $border;
		border-top: 1px solid $border;
		padding: 3px;
	}
	.navbar-toggler:focus {
		box-shadow: none;
	}
}

@media (min-width: 768px) {
