	}
}
.light-layout {
 	display: none;
}

.construction-image .header-setting-icon {
	position: absolute;
    left: auto;
    right: 50px;
    top: 50px;
	background-color: $white-1;
	border-radius: 50%;
}

@media (max-width: 767px) {
	.construction-image .header-setting-icon {
		left: auto;
		right: 20px;
		top: 10px;
	}
}

#individual , #group {
	.richText .richText-toolbar ul li a {
		border: 0;
	}
}
.mail-option .chk-all {
	margin: 5px 5px 5px 0
}
.card.card-collapsed {
	border-radius: 3px;
	.card-header {
		border-bottom: 0;
	}
