.side-menu__item {
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	font-size: 13px;
	font-weight: 400;
	padding: 10px 0px;
	-webkit-transition: border-left-color 0.3s ease, background-color 0.3s ease;
	-o-transition: border-left-color 0.3s ease, background-color 0.3s ease;
	transition: border-left-color 0.3s ease, background-color 0.3s ease;
	color: #6c81a6;
}

.app-sidebar .mCSB_draggerContainer {
	right: -11px;
}

.side-menu__icon {
	-webkit-box-flex: 0;
	-ms-flex: 0 0 auto;
	flex: 0 0 auto;
	width: 25px;
	line-height: 25px !important;
	text-align: center;
	border-radius: 3px;
	margin-right: 10px;
	font-size: 17px !important;
}

.side-menu__label {
	white-space: nowrap;
	-webkit-box-flex: 1;
	-ms-flex: 1 1 auto;
	flex: 1 1 auto;
}

.side-menu li ul li a {
	border-top: 0;
	border-bottom: 0;
}

.app-sidebar {
	.side-menu li a.active {
		background: none;
	}
	.slide-menu li.active > a {
		font-weight: 500;
	}
}

.slide-menu li {
	position: relative;
	&.active {
		&:before {
			content: "";
			position: absolute;
			width: 15px;
			height: 2px;
			background: $border;
			left: -20px;
			top: 15px;
			display: block;
		}
		&:after {
			content: "";
			position: absolute;
			width: 8px;
			height: 8px;
			left: -10px;
			top: 12px;
			display: block;
			border-radius: 50%;
		}
	}
	ul {
		li {
			font-size: 12px !important;
			font-weight: 300;
			border-bottom: 0px solid rgba(0, 0, 0, 0.03);
		}
		a {
			padding: 10px 10px 10px 65px;
		}
	}
}

.side-menu .slide-menu li a {
	font-size: 13px;
	font-weight: 400;
}

.slide ul li a {
	color: #153039;
	font-weight: 400;
}

.app-sidebar-footer {
	clear: both;
	display: block;
	color: $white;
	position: fixed;
	bottom: 0px;
	width: 228px;
	z-index: 999;
	border-top: 1px solid #e9ecf3;
	a {
		padding: 10px 0;
		text-align: center;
		width: 20%;
		color: $white;
		font-size: 15px;
		display: block;
		float: left;
		cursor: pointer;
		border-right: 1px solid #e9ecf3;
		border-bottom: 1px solid #e9ecf3;
		&:hover {
			background: rgba(255, 255, 255, 0.02);
		}
	}
}

.slide ul ul a {
	font-size: 1em !important;
	padding: 10px 10px 10px 25px !important;
}

.slide ul ul ul a {
    font-size: 1em !important;
    padding: 10px 10px 10px 40px !important;
}

.app-sidebar ul li a {
	color: #8492a6;
	font-weight: 500;
}

.slide.is-expanded {
	.slide-menu {
		// max-height: 100%;
		display: block;
	}
	.angle {
		-webkit-transform: rotate(90deg);
		-ms-transform: rotate(90deg);
		transform: rotate(90deg);
	}
}

.slide-menu {
	// max-height: 0;
	display: none;
	overflow: hidden;
	-webkit-transition: max-height 0.3ms ease;
	-o-transition: max-height 0.3ms ease;
	transition: max-height 0.3ms ease;
	padding: 0;
	font-size: 0.8rem !important;
	padding-left: 20px;
	margin-left: 12px;
	border-left: 1px solid $border;
}

.slide-item {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	padding: 6px 7px;
	font-size: 13px;
	color: #6c81a6;
	z-index: 999 !important;
}

.app-sidebar .side-menu .slide-menu li:last-child {
	border-bottom: 0;
}

.slide-item .icon {
	margin-right: 5px;
}

.angle {
	-webkit-transform-origin: center;
	-ms-transform-origin: center;
	transform-origin: center;
	-webkit-transition: -webkit-transform 0.3s ease;
	transition: -webkit-transform 0.3s ease;
	-o-transition: transform 0.3s ease;
	transition: transform 0.3s ease;
	transition: transform 0.3s ease, -webkit-transform 0.3s ease;
	margin-right: 10px;
}

.dropdown-menu {
	&.dropdown-menu-end {
		left: auto !important;
		// right: 0 !important;
	}
}

.dropdown-item {
	.fa,
	.icon {
		vertical-align: middle;
	}
}

